@import "https://fonts.googleapis.com/css2?family=Domine:wght@400..700&display=swap";
:root {
  --accent: #00e6bf;
  --bg1: #764b36;
  --bg1-text: #fcfaf8;
  --bg2: #9b4922;
  --bg2-text: #fcfaf8;
  --bg3: #d1774c;
  --bg3-text: #2c1f17;
  --bg4: #d8baac;
  --bg4-text: #2c1f17;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Domine, serif;
}

html {
  background-color: var(--bg4);
  color: var(--bg4-text);
  font-size: 62.5%;
  line-height: 1.6;
}

p, button, input, select, textarea, label {
  font-size: 1.6rem;
}

h1 {
  font-size: 3.2rem;
}

h2 {
  font-size: 2.4rem;
}

h3 {
  font-size: 2.2rem;
  line-height: 2.4rem;
}

button, select {
  cursor: pointer;
}

header {
  background-color: var(--bg3);
  height: 6.4rem;
  color: var(--bg3-text);
  justify-content: space-between;
  align-items: center;
  padding: .8rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  && button {
    background-color: #0000;
    border: .4rem solid #0000;
    border-bottom-color: #2c1f1700;
    padding: .4rem;
    font-weight: bold;

    &&:hover {
      border-bottom: .4rem solid #2c1f1780;
    }

    &&:focus-visible {
      border: .4rem solid #2c1f1780;
      outline: .2rem solid #fff;
    }

    &&:active {
      border-bottom: .4rem solid var(--bg4-text);
    }
  }
}

main {
  flex-direction: column;
  align-items: center;
  max-width: 1100px;
  margin: 6.4rem auto auto;
  padding: .8rem;
  display: flex;
}

#logSection {
  background-color: var(--bg2);
  color: var(--bg2-text);
  width: 100%;
  padding: .8rem .8rem 0;

  && div:first-of-type {
    grid-template-columns: repeat(2, 1fr);
    gap: .8rem;
    padding-bottom: .8rem;
    display: grid;

    && select {
      background-color: var(--bg1);
      color: var(--bg1-text);
      border: .4rem solid #0000;
      padding: .4rem;

      &&:hover {
        border: .4rem solid #2c1f1780;
      }

      &&:focus-visible {
        border: .4rem solid #2c1f1780;
        outline: .2rem solid #fff;
      }
    }
  }

  && ul {
    grid-template-columns: repeat(1, 1fr);
    gap: .8rem;
    list-style: none;
    display: grid;

    && li {
      background-color: var(--bg1);
      color: var(--bg1-text);
      padding: .8rem;

      && div {
        grid-template-columns: auto 12.2rem;
        display: grid;

        && :nth-child(2n) {
          text-align: right;
        }

        && p:first-of-type {
          color: var(--accent);
          flex-direction: column;
          justify-content: flex-start;
          font-size: 2rem;
          line-height: 2.4rem;
          display: flex;
        }

        && p:nth-of-type(2) {
          font-weight: bold;
        }

        &&:last-of-type {
          grid-template-columns: repeat(2, 1fr);
          gap: .8rem;
          margin-top: .4rem;

          && button {
            text-align: center;
            border: .2rem solid var(--bg4-text);
            background-color: var(--bg2);
            color: var(--bg2-text);
            padding: .8rem;

            &&:hover {
              background-color: #d1622e;
            }

            &&:focus-visible {
              background-color: #d1622e;
              outline: .2rem solid #fff;
            }

            &&:active {
              background-color: #7d3b1c;
            }
          }
        }
      }
    }
  }

  && span {
    background-color: var(--bg4);
    height: 1.6rem;
    margin: .8rem 1.6rem 0;
    display: block;
  }
}

.formSection {
  background-color: #00000080;
  padding: .8rem;
  position: fixed;
  inset: 0;

  && form {
    background-color: var(--bg1);
    color: var(--bg1-text);
    border: .4rem solid var(--bg2);
    flex-direction: column;
    gap: .4rem;
    max-width: 60rem;
    margin: 7.2rem auto auto;
    padding: .8rem;
    display: flex;

    && label, && div {
      background-color: var(--bg4);
      color: var(--bg4-text);
      border: .2rem solid var(--bg4-text);
      align-items: center;
      gap: .4rem;
      padding: 0 0 0 .4rem;
      display: flex;

      && input, && select, && textarea {
        resize: none;
        color: var(--bg4-text);
        background-color: #0000;
        border: #0000;
        outline: none;
        flex-grow: 1;
        padding: .8rem .4rem;

        &&:hover {
          background-color: #fff4;
        }

        &&:focus {
          background-color: #ffffff80;
        }
      }
    }

    && div {
      padding: 0;
    }

    && textarea {
      height: 7.6rem;
    }

    && button {
      text-align: left;
      border: .2rem solid var(--bg4-text);
      background-color: var(--bg2);
      color: var(--bg2-text);
      padding: .8rem;

      &&:hover {
        background-color: #d1622e;
      }

      &&:focus-visible {
        background-color: #d1622e;
        outline: .2rem solid #fff;
      }

      &&:active {
        background-color: #7d3b1c;
      }
    }

    && div:first-of-type {
      color: var(--bg1-text);
      background-color: #0000;
      border: none;
      justify-content: space-between;
      display: flex;

      && button {
        aspect-ratio: 1;
        justify-content: center;
        align-items: center;
        font-size: 2.8rem;
        display: flex;
      }
    }
  }
}

@media (width >= 720px) {
  #logSection ul {
    grid-template-columns: repeat(2, 1fr) !important;
  }
}
